<template>
  <div>
    <el-row>
      <el-col>
        <el-table :data="posts" style="width: 100%">
          <el-table-column label="岗位ID" prop="postID" v-if="show = false"></el-table-column>
          <el-table-column label="岗位名称" prop="postName"></el-table-column>
          <el-table-column align="right">
            <template slot="header">
              <el-button size="small" type="primary" @click="dialogFormVisible1 = true">
                <i class="el-icon-circle-plus-outline" />添加岗位
              </el-button>
            </template>
            <template slot-scope="scope">
              <el-button size="small" type="success" @click="upadte(scope.row)" round>
                <i class="el-icon-edit-outline" />修改
              </el-button>
              <el-button size="small" type="danger" @click="Delete(scope.row)" round>
                <i class="el-icon-delete-solid" />删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <el-pagination
          background
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="5"
          layout="total, prev, pager, next"
          :total="total"
        ></el-pagination>
      </el-col>
    </el-row>
    <!-- 新增模态框 -->
    <el-dialog title="新增" :visible.sync="dialogFormVisible1">
      <el-form ref="form1" :rules="rules" :model="form">
        <el-form-item label="岗位名称" :label-width="formLabelWidth" prop="postName">
          <el-input v-model="form.postName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('form1')">重 置</el-button>
        <el-button type="primary" @click="Post('form1')">确 定</el-button>
      </div>
    </el-dialog>
    <!-- 修改模态框 -->
    <el-dialog title="修改" :rules="rules" :visible.sync="dialogFormVisible2">
      <el-form ref="form2" :model="form">
        <el-form-item label="岗位名称" :label-width="formLabelWidth" prop="postName">
          <el-input v-model="form.postName" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetForm('form2')">重 置</el-button>
        <el-button type="primary" @click="Put('form2')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Qs from "qs";

export default {
  inject: ["reload"],
  data() {
    return {
      currentPage: 1,
      total: 0,
      posts: [],
      updateID: 0,
      dialogFormVisible1: false,
      dialogFormVisible2: false,
      form: {
        postName: ""
      },
      formLabelWidth: "120px",
      rules: {
        postName: [
          { required: true, message: "请输入部门名称", trigger: "blur" },
          { min: 3, max: 20, message: "长度在 3 到 20 个字符", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    Post(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("post对象：" + this.form);
          var api = "http://localhost:8888/post/add";
          this.axios
            .post(api, Qs.stringify(this.form))
            .then(response => {
              console.log(response);
              if (response.data == 1) {
                this.$message({
                  showClose: true,
                  message: "操作成功",
                  type: "success"
                });
                this.dialogFormVisible1 = false;
                this.reload();
              }
            })
            .catch(error => {
              console.log(error);
              this.$message({
                showClose: true,
                message: "操作失败，请联系维护人员",
                type: "error"
              });
            });
        } else {
          this.$message({
            showClose: true,
            message: "请检查数据是否填写正确！",
            type: "error"
          });
        }
      });
    },
    upadte(row) {
      this.dialogFormVisible2 = true;
      this.updateID = row.postID;
      this.form.postName = row.postName;
    },
    Put(formName) {
      console.log(formName);
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log("put对象：" + this.form);
          var api = "http://localhost:8888/post/" + this.updateID;
          this.axios
            .put(api, Qs.stringify(this.form))
            .then(response => {
              console.log(response);
              if (response.data == 1) {
                this.$message({
                  showClose: true,
                  message: "操作成功",
                  type: "success"
                });
                this.dialogFormVisible2 = false;
                this.reload();
              }
            })
            .catch(error => {
              console.log(error);
              this.$message({
                showClose: true,
                message: "操作失败，请联系维护人员",
                type: "error"
              });
            });
        } else {
          this.$message({
            showClose: true,
            message: "请检查数据是否填写正确！",
            type: "error"
          });
        }
      });
    },
    Delete(row) {
      console.log("delete对象：" + row);
      var api = "http://localhost:8888/post/" + row.postID;
      this.axios
        .delete(api)
        .then(response => {
          console.log(response);
          this.$message({
            showClose: true,
            message: "操作成功",
            type: "success"
          });
          this.reload();
        })
        .catch(error => {
          console.log(error);
        });
    },
    handleCurrentChange(current) {
      this.currentPage = current;
      this.$route.params.current = current;
      var api = "http://localhost:8888/post/" + current + "/" + 5;
      this.axios
        .get(api)
        .then(response => {
          console.log("分页响应对象：" + response);
          this.posts = response.data.records;
        })
        .catch(error => {
          console.log(error);
        });
    },
    getData() {
      var api =
        "http://localhost:8888/post/" +
        this.$route.params.current +
        "/" +
        this.$route.params.size;
      this.axios
        .get(api)
        .then(response => {
          console.log(response);
          this.posts = response.data.records;
          this.total = response.data.total;
        })
        .catch(error => {
          console.log(error);
        });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  created() {
    this.getData();
  }
};
</script>

<style lang="scss" scoped>
</style>